import { Component, ViewEncapsulation } from '@angular/core';
import { BlockContentComponent, direction } from '../../templates/block-content/block-content.component';
import { CommonModule } from '@angular/common';
import { AppComponent } from '../../app.component';

@Component({
  selector: 'app-about',
  standalone: true,
  imports: [BlockContentComponent, CommonModule],
  templateUrl: './about.component.html',
  styleUrl: './about.component.less',
})
export class AboutComponent {
  constructor(private app: AppComponent) {}

  protected readonly dir = direction;
  protected profile = this.app.header;
  protected contacts = this.app.contacts;
  protected maps = this.app.maps;
  protected titleJoin = 'Cabang ' + this.profile.serviceName + ' Kami';

  protected tracker(i: number, x: any) {
    return x;
  }
}
